@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,900&display=swap&subset=latin-ext);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Raleway', sans-serif;
} 

body {
    background-color: #EAEAEA;
    overflow-x: hidden;
}
.navbar{display:flex;justify-content:space-between;align-items:center;padding:0 5% 0 2%;height:60px;background:linear-gradient(270.36deg, #B23873 7.27%, rgba(107,57,81,0) 38.84%, rgba(255,255,255,0) 68.41%),#2E2E2E}.navbar h1.app-title{font-size:18px;color:#FFF}.navbar .logo-wrapper{display:flex;align-items:center}.navbar .logo-wrapper .logo{-webkit-transform:rotate(-2deg);transform:rotate(-2deg);padding:0 10px 0 0}.navbar .btn{position:absolute;top:0;right:0;padding:15px 20px;cursor:pointer}.navbar ul{display:flex;flex-direction:column;align-items:center;position:absolute;right:0;top:0;padding:30% 0;height:100vh;width:65%;background-color:#B23873;-webkit-transform:translateX(100%);transform:translateX(100%);opacity:0;-webkit-transition:ease-in .15s;transition:ease-in .15s}.navbar ul li{font-weight:900;padding:15px 20px;list-style:none}.navbar ul li a{color:#FFFFFF;font-size:16px;text-decoration:none}.navbar ul.active{-webkit-transform:translateX(0);transform:translateX(0);opacity:1}.navbar i{padding:10px}@media (min-width: 1024px){.navbar .btn{display:none}.navbar ul{justify-content:flex-end;-webkit-transform:translateX(0);transform:translateX(0);opacity:1;display:flex;flex-direction:row;align-items:center;position:absolute;right:0;top:0;padding:0 0;height:60px;width:100%;background-color:transparent}}

h4{font-size:14px;font-weight:400}.form-container{padding:30px}form{width:90%;min-width:300px;max-width:420px}form input{margin:15px 0;padding:0 10px;background:#C4C4C4;border:1px solid #B1B1B1;height:50px;width:100%;font-size:40px;font-weight:300;font-size:16px;line-height:16px}form input:focus::-webkit-input-placeholder{color:transparent;background-color:#CDBCC4}form input:focus::-moz-placeholder{color:transparent;background-color:#CDBCC4}form input:focus:-ms-input-placeholder{color:transparent;background-color:#CDBCC4}form input:focus::-ms-input-placeholder{color:transparent;background-color:#CDBCC4}form input:focus::placeholder{color:transparent;background-color:#CDBCC4}div.contacts-container{display:flex;align-items:center;justify-content:center;flex-direction:column;padding:10% 5%;width:100%;max-width:520px;background-color:#EFEFEF;box-shadow:0px 0px 10px rgba(0,0,0,0.06)}div.contacts-container .contact-item{margin:10px 0;padding:0 20px;background:#f1f0f0;border:1px solid rgba(0,0,0,0.06);width:100%}div.contacts-container .contact-item h3{padding:15px 0;font-weight:900;font-size:24px;line-height:28px;color:#2E2E2E}div.contacts-container .contact-item ul{list-style:none}div.contacts-container .contact-item ul li i{padding:5px;font-size:18px;color:#2E2E2E}div.contacts-container .contact-item .submit{margin:15px 5px;display:inline-block;background:#2E2E2E;border-radius:3px;width:100px;height:30px;border:none;color:#FFF;font-weight:900;font-size:12px;cursor:pointer}

.container{display:flex;flex-direction:column;justify-content:center;align-items:center;min-width:80vw}.container>div{padding:5%}@media (min-width: 1024px){.container{flex-direction:row-reverse}}

div.form-container{display:flex;flex-direction:column;align-items:center;padding:10% 5%;width:100%}div.form-container h1{padding:30px 0;font-size:46px;color:#2E2E2E;text-align:center}div.form-container form{width:100%}div.form-container form .form-group{display:flex;align-items:center;justify-content:center}div.form-container form .form-group input{margin:15px 0;padding:0 10px;background:#C4C4C4;border:1px solid #B1B1B1;height:50px;width:100%;font-size:40px;font-weight:300;font-size:16px;line-height:16px;max-width:420px}div.form-container form .form-group input:focus::-webkit-input-placeholder{color:transparent;background-color:#CDBCC4}div.form-container form .form-group input:focus::-moz-placeholder{color:transparent;background-color:#CDBCC4}div.form-container form .form-group input:focus:-ms-input-placeholder{color:transparent;background-color:#CDBCC4}div.form-container form .form-group input:focus::-ms-input-placeholder{color:transparent;background-color:#CDBCC4}div.form-container form .form-group input:focus::placeholder{color:transparent;background-color:#CDBCC4}div.form-container form .submit{margin:15px auto;display:block;background:rgba(178,56,115,0.9);border-radius:3px;width:115px;height:30px;border:none;color:#FFF;font-weight:900;font-size:12px;cursor:pointer}

div.alert-danger{margin:0 auto;height:50px;-webkit-transform:translateY(10vh);transform:translateY(10vh);background:#E45858;width:80%;color:#FFF;font-size:14px;text-align:center;line-height:50px}div.alert-danger i{padding:5px;font-size:18px}

