h4 {
    font-size: 14px;
    font-weight: 400;
}
.form-container {
    padding: 30px;
}
form {
    width: 90%;
    min-width: 300px;
    max-width: 420px;
    input {
        margin: 15px 0;
                padding: 0 10px;
                background: #C4C4C4;
                border: 1px solid #B1B1B1;
                height: 50px;
                width: 100%;
                font-size: 40px;
                font-weight: 300;
                font-size: 16px;
                line-height: 16px;
    }
    input:focus::placeholder {
        color: transparent;
        background-color: #CDBCC4;
    }
}

div.contacts-container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 10% 5%;
    width: 100%;
    max-width: 520px;
    background-color: #EFEFEF;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.06);
    .contact-item {
        margin: 10px 0;
        padding: 0 20px;
        background: rgb(241, 240, 240);
        border: 1px solid rgba(0, 0, 0, 0.06);
        width: 100%;
        h3 {
            padding: 15px 0;
            font-weight: 900;
            font-size: 24px;
            line-height: 28px;
            color: #2E2E2E;
        }
        ul {
            list-style: none;
            li {
                i {
                    padding: 5px;
                    font-size: 18px;
                    color: #2E2E2E;
                }
            }
        }
        .submit {
            margin: 15px 5px;
            display: inline-block;
            background: #2E2E2E;
            border-radius: 3px;
            width: 100px;
            height: 30px;
            border: none;
            color: #FFF;
            font-weight: 900;
            font-size: 12px;
            cursor: pointer;
        }
    }
}