div.form-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10% 5%;
    width: 100%;
    h1 {
        padding: 30px 0;
        font-size: 46px;
        color: #2E2E2E;
        text-align: center;
    }
    form {
        width: 100%;
        .form-group {
            display: flex;
            align-items: center;
            justify-content: center;
            input {
                margin: 15px 0;
                padding: 0 10px;
                background: #C4C4C4;
                border: 1px solid #B1B1B1;
                height: 50px;
                width: 100%;
                font-size: 40px;
                font-weight: 300;
                font-size: 16px;
                line-height: 16px;
                max-width: 420px;
            }
            input:focus::placeholder {
                color: transparent;
                background-color: #CDBCC4;
            }
        }
        .submit {
            margin: 15px auto;
            display: block;
            background: rgba(178, 56, 115, 0.9);
            border-radius: 3px;
            width: 115px;
            height: 30px;
            border: none;
            color: #FFF;
            font-weight: 900;
            font-size: 12px;
            cursor: pointer;
        }
    }
}