.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 0 5% 0 2%;

    height: 60px;
    background: linear-gradient(270.36deg, #B23873 7.27%, rgba(107, 57, 81, 0) 38.84%, rgba(255, 255, 255, 0) 68.41%), #2E2E2E;
    h1.app-title {
        font-size: 18px;
        color: #FFF;
    }
    .logo-wrapper {
        display: flex;
        align-items: center;
        .logo {
            transform: rotate(-2deg);
            padding: 0 10px 0 0;
        }
    }
    .btn {
        position: absolute;
        top: 0;
        right: 0;
        padding: 15px 20px;
        cursor: pointer;
    }
    ul {
        display: flex;
        flex-direction: column;
        align-items: center;
        position: absolute;
        right: 0;
        top: 0;
        padding: 30% 0;
        height: 100vh;
        width: 65%;
        background-color: #B23873;
        transform: translateX(100%);
        opacity: 0;
        transition: ease-in .15s;
        li {
            font-weight: 900;
            padding: 15px 20px;
            list-style: none;
            a {
                color: #FFFFFF;
                font-size: 16px;
                text-decoration: none;
            }
        }
    }
    ul.active {
        transform: translateX(0);
        opacity: 1;
    }
    i {
        padding: 10px;
    }
} 

@media (min-width: 1024px) {
     .navbar {
         .btn {
             display: none;
         }
         ul {
            justify-content: flex-end;
            transform: translateX(0);
            opacity: 1;
            display: flex;
            flex-direction: row;
            align-items: center;
            position: absolute;
            right: 0;
            top: 0;
            padding: 0 0;
            height: 60px;
            width: 100%;
            background-color: transparent;
         }
     }
}